oftware application that will allow you to analyze the information received on major social networks (Twitter, Facebook, etc.) and compare it with information from official websites of the MoH or WHO. This will make it possible to objectify the real situation with COVID 19 and reduce panic among the population and develop covinophobia. For example, on the social network, certain information is published about the number of patients, or about the death rate, or other COVID-related information. The program seeks confirmation of this information by key phrases, dates, etc. on the main and official sites of medicine and WHO and the Ministry of Health and others. In its absence, the denial of the fake is immediately published. This will significantly reduce panic among the population and make people only use truthful information. The results of implementation of this idea allow education operators and health operators to increase theirs professional output and find the sources of fake news. ")])])])}]

